import React, { Fragment } from "react";
import { connect } from "react-redux";
import {
  Auth,
  Basic,
  TeacherData,
  TeacherDataRole,
  FC_GetTeachersBySchool,
  FC_DisableUser,
  FT_DisableUser,
  // FC_GetSchoolDetailsRegister,
  API_DataGetSchoolRegister,
  FC_GetDisabledUsers,
  DisabledUser,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import DashboardContainer from "../../components/DashboardContainer/DashboardContainer";
import Heading from "../../components/Heading/Heading";
// import InputSelect from "../../components/Inputs/InputSelect";
import TextInput from "../../components/Inputs/TextInput";
import Loading from "../../components/Loading/Loading";
// import Modal, { ModalSize, Themes } from "../../components/Modal/Modal";
// import { UpdateStuffDetails } from "../../components/UpdateStuffDetails/UpdateStuffDetails";
// import { UserProfile } from "../../containers/UserProfile/UserProfile";
import { StoreState } from "../../reducers";
import { search } from "../../utils/functions";
import { AssignStaffToSchool } from "../UserProfile/AssignStaffToSchool";

// props for the component
interface AppProps {
  auth: Auth;
}

interface AppState {
  loading: boolean;
  error: {
    target: "main-success" | "main-error" | null;
    msg: string;
  };
  data: DisabledUser[];
  search: string;
  selected_user: DisabledUser | null;
}

class _DisabledUsers extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      data: [],
      error: {
        target: null,
        msg: "",
      },
      search: "",
      loading: true,
      selected_user: null,
    };
  }

  componentDidMount = () => {
    this.startApp();
  };

  startApp = () => {
    this.setState({
      loading: true,
      error: {
        target: null,
        msg: "",
      },
    });

    FC_GetDisabledUsers((status, data, msg) => {
      if (status === true && data !== null) {
        // console.log({ status, data, msg });
        this.setState({
          loading: false,
          data: data,
        });
      } else {
        this.setState({
          loading: false,
          error: {
            msg: msg,
            target: "main-error",
          },
        });
      }
    });
  };

  render() {
    let RequestsList: DisabledUser[] = search(
      this.state.data,
      this.state.search,
      {
        school_code: true,
        district_code: true,
        position_code: true,
        school_name: true,
        district_name: true,
        full_name: true,
        first_name: true,
        last_name: true,
        email: true,
        middle_name: true,
        nid: true,
        phone_numbers: true,
        staff_code: true,
      }
    );

    if (this.state.loading === true)
      return (
        <div className="p-8 text-center">
          <div className="mx-auto">
            <Loading />
          </div>
        </div>
      );

    if (
      this.state.selected_user !== null &&
      this.state.selected_user.user_id !== null
    )
      return (
        <AssignStaffToSchool
          user_id={this.state.selected_user.user_id}
          // userInfo={this.state.userInfo}
          userDetails={{
            email: this.state.selected_user.email + "",
            full_name: this.state.selected_user.full_name + "",
            nid: this.state.selected_user.nid + "",
            staff_code: this.state.selected_user.staff_code + "",
          }}
          close={() =>
            this.setState({
              selected_user: null,
            })
          }
          // district_code={
          //   this.state.userInfo?.staffDetails.workInfo?.school_district
          // }
          // current_position={
          //   this.state.userInfo?.staffDetails.workInfo.
          // }
          // district_name={this.state.userInfo?.staffDetails.workInfo?.}
          // school_code={this.state.userInfo?.staffDetails.workInfo?.school_code}
          // school_name={this.state.userInfo?.staffDetails.workInfo?.school_name}
        />
      );

    return (
      <Fragment>
        <>
          {this.state.error.target === "main-success" && (
            <Alert theme="success" fixed>
              {this.state.error.msg}
            </Alert>
          )}

          {this.state.error.target === "main-success" && (
            <Alert theme="success" fixed>
              {this.state.error.msg}
            </Alert>
          )}

          <DashboardContainer>
            <div className="flex items-center">
              <div className="flex-1">
                <Heading>Disabled users</Heading>
              </div>
            </div>

            <div className="mt-3">
              <div className="border border-t mt-2 mb-1 flex items-center pl-3 pr-2 rounded">
                <div className="flex-1 pr-2">
                  <TextInput
                    error=""
                    label="Search"
                    name=""
                    type="search"
                    onChange={(e) => this.setState({ search: e.target.value })}
                    placeholder="Search by district name, staff code"
                    value={this.state.search}
                    size="sm"
                  />
                </div>

                <div className="p-2 px-3 ml-3 bg-accent-800 bg-opacity-30 rounded">
                  <label className="">Results:</label>
                  <h4 className="text-2xl -mt-1 font-bold">
                    {RequestsList.length}
                  </h4>
                </div>
              </div>
              <table className="w-full text-left table-auto">
                <thead>
                  <tr>
                    <th className="p-2 border">#</th>
                    <th className="p-2 border">NID</th>
                    <th className="p-2 border">First name</th>
                    <th className="p-2 border">Last name</th>
                    <th className="p-2 border">Middle name</th>
                    <th className="p-2 border">Full name</th>
                    <th className="p-2 border">Phone number</th>
                    <th className="p-2 border"></th>
                  </tr>
                </thead>
                <tbody>
                  {RequestsList.length <= 0 ? (
                    <tr>
                      <td
                        colSpan={8}
                        className="p-8 border rounded border-opacity-25 animate__animated animate__fadeIn text-center"
                      >
                        <Heading size="md" className="opacity-60 font-light">
                          No results found
                        </Heading>
                      </td>
                    </tr>
                  ) : (
                    RequestsList.map((req, i) => {
                      return (
                        <Fragment key={i}>
                          <tr className="group border rounded hover:bg-blue-500 hover:bg-opacity-20 text-sm">
                            <td className="p-2 border" key={i}>
                              {i + 1}
                            </td>
                            <td className="p-2 border">{req.nid}</td>
                            <td className="p-2 border">{req.first_name}</td>
                            <td className="p-2 border">{req.middle_name}</td>
                            <td className="p-2 border">{req.last_name}</td>
                            <td className="p-2 border">{req.full_name}</td>
                            <td className="p-2 border">{req.phone_numbers}</td>
                            <td className=" text-center border">
                              <Button
                                size="sm"
                                theme="accent-light"
                                className="w-full"
                                onClick={() =>
                                  this.setState({ selected_user: req })
                                }
                              >
                                Open profile
                              </Button>
                            </td>
                          </tr>
                        </Fragment>
                      );
                    })
                  )}
                </tbody>
              </table>
            </div>
          </DashboardContainer>
        </>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  basic,
}: StoreState): { auth: Auth; basic: Basic } => {
  return { auth: auth, basic: basic };
};

export const DisabledUsers = connect(mapStateToProps, { FC_DisableUser })(
  _DisabledUsers
);
